/** Global page styling */
nav.top-nav
{
  background-color: #294d67;
  color: white;
  line-height: 40px;
  padding: 0 10px;

  ul
  {
    list-style: none;
    padding: 0;
    margin: 0;

    li
    {
      display: inline-block;
      text-align: center;
      margin-right: 15px;

      a
      {
        color: white;
      }
    }
  }
}

.loader
{
  position: fixed;
  left: 43%;
  top: 250px;
  z-index: 200;
  opacity: 0.6;
  display: none;
}

.loader-background
{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  z-index: 100;
  display: none;
  background-color: black;
}

.error-text
{
  color: red;
  font-weight: bold;
  text-decoration: underline;
}

/** Home page styling */
div.home-index
{
  div.search-section
  {
    margin-top: 140px;

    div.origin-location-address
    {
      margin-top: 30px;
    }

    input.search-input
    {
      padding: 21px 12px;
      font-size: 19px;
    }

    .search-button-section
    {
      text-align: center;

      .search-button
      {
        width: 100px;
        font-size: 18px;
      }
    }

    div.source-location-section
    {
      margin-bottom: 20px;
      margin-top: 20px;

      ul
      {
        list-style: none;
        padding: 0;
        margin: 0;

        li
        {
          display: inline-block;
          margin-right: 10px;
        }
      }
    }

    img
    {
      width: 43%;
      display: block;
      margin: 0 auto;
      margin-bottom: 14px;
    }
  }
}

/** Login page styling */
div.auth-login
{
  img
  {
    width: 40%;
    display: block;
    margin: 0 auto;
    margin-top: 130px;
  }

  .login-section
  {
    margin-top: 65px;

    h2 { text-align: center; }

    .login-form-section
    {
      label
      {
        font-size: 12px;
      }

      #remember-me-checkbox
      {
        margin-right: 3px;
      }

      .sign-in-button
      {
        margin-right: 10px;
      }
    }
  }
}

/** Registration page styling */
div.user-registration
{
  margin-top: 60px;

  input[type='checkbox']
  {
    margin-right: 4px;
  }

  div.agreement-section
  {
    font-size: 15px;

    iframe
    {
      border: 1px solid #3a87ad;
      border-radius: 4px;
      width: 100%;
      padding: 7px;
      height: 600px;
    }

    div {margin-bottom: 10px}
  }
}

/** Search results page styling */
div.search-results
{
  div.mass-interact-section
  {
    div.interact-option
    {
      border-bottom: 1px solid #CCCCCC;
      padding-bottom: 20px;
      margin-bottom: 20px;
    }

    div.interact-option:last-of-type
    {
      border-bottom: none;
    }
  }

  div.logo-section
  {
    margin-top: 70px;
    margin-bottom: 30px;

    img
    {
      display: block;
      max-width: 200px;
      margin: 0 auto;
    }
  }

  h1
  {
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 25px;
  }

  div.source-location-section
  {
    margin-top: 20px;
    margin-bottom: 20px;

    ul
    {
      list-style: none;
      padding: 0;
      margin: 0;

      li
      {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }

  div.search-section
  {
    border: 1px solid #38B44A;
    padding: 20px;
    border-radius: 5px;
    background-color: #EAFFED;
    margin-bottom: 15px;
  }

  div.search-result
  {
    margin-bottom: 28px;
    padding-bottom: 24px;
    border-bottom: 1px solid #CCCCCC;

    div.result-buttons
    {
      margin-top: 12px;

      button
      {
        font-size: 11px;
        padding: 3px 8px;

      }
    }

    a.search-result-img-link
    {
      display: inline-block;
      vertical-align: top;
      max-width: 80px;
      max-height: 80px;
      margin-right: 6px;
      margin-left: 12px;

      img
      {
        max-width: 100%;
        max-height: 100%;
      }
    }

    div.search-result-info
    {
      vertical-align: top;
      display: inline-block;
      font-size: 12px;
      max-width: 70%;

      div.distance
      {
        font-weight: bold;
      }

      div.status
      {
        font-weight: bold;

        i.available
        {
          color: #00a700;
        }

        i.unavailable
        {
          color: #df0000;
        }
      }

      p
      {
        margin: 0;
        margin-bottom: 6px;
      }

      div.ratings
      {
        font-weight: bold;

        span.rating
        {
          color: #ffb700;
          margin-left: 3px;
          padding-left: 5px;
          border-left: 1px solid #9E9E9E;
        }
      }

      h5
      {
        margin: 0;
        margin-bottom: 4px;
        font-weight: bold;
        font-size: 13px;
      }
    }
  }

  div.search-result:last-of-type
  {
    border-bottom: none;
  }
}

/** My Services page */
div.my-services-section
{
  margin-top: 60px;

  div.tab-content
  {
    margin-top: 25px;

    #services
    {
      .top-section
      {
        .takt-list
        {
          .takt-listing
          {
            padding: 10px;

            h4
            {
              font-size: 14px;
              font-weight: bold;
              margin: 0;
            }

            p.takt-body
            {
              font-size: 13px;
              margin-bottom: 5px;
            }

            p.takt-date
            {
              font-size: 10px;
            }

            ul.button-bar
            {
              padding: 0;
              margin: 0;
              list-style: none;

              li
              {
                display: inline-block;

                .btn
                {
                  padding: 2px 4px;
                  font-size: 12px;
                }
              }
            }
          }

          .pagination
          {
            margin-left: 13px;
          }
        }

        .services-list
        {
          .service-listing
          {
            cursor: pointer;

            h4
            {
              margin-top: 0;
              font-size: 14px;
              font-weight: normal;

              a
              {
                text-decoration: none;
                color: #6f6f6f;
              }
            }

            .button-bar
            {
              ul
              {
                list-style-type: none;
                padding: 0;
                margin: 0;

                li
                {
                  display: inline-block;
                  .btn
                  {
                    font-size: 11px;
                    padding: 3px 8px;
                  }
                }
              }
            }
          }

          .service-listing.active
          {
            background-color: #D9EDF7;
            color: white;
            border: 1px solid #C3E0EA;

            h4
            {
              a {color: #3a87ad;}
            }
          }
        }

        .service-status
        {
          .service-status-listing
          {
            h4
            {
              font-size: 13px;
              font-weight: bold;
              padding: 0;
              margin: 0;
              color: #2d6987;
            }

            p
            {
              font-size: 12px;
              .fa.active
              {
                color: #00a400;
              }
              .fa.inactive
              {
                color: #e60000;
              }
            }

            .fa
            {
              font-size: 12px;
            }
          }
        }
      }

      div.ratings
      {
        h3
        {
          margin: 1px 1px 17px 0px;
          border-bottom: 1px solid black;
          padding-bottom: 8px;
        }

        .rating
        {
          margin-bottom: 10px;
          padding-bottom: 10px;
          border-bottom: 1px solid #cccccc;

          p
          {
            padding: 0;
            margin: 0;
          }

          h4
          {
            font-size: 14px;
            font-weight: bold;
            margin: 0 0 3px 0;
          }
        }
      }
    }
  }

  .edit-service
  {
    .modal-dialog
    {
      width: 80%;
    }
  }
}